<template>
  <right-page :header="rightPageHeader" :hide-content-padding="true">
    <div slot="content">
      <div class="section">
        <ld-input
          class="section-content"
          label="Form Handler URL(HTTPS only)"
          error-message="Invalid HTTPS URL."
          :error="!isUrlLegal"
          v-model="endpointURL"
        />
      </div>
      <footer class="slds-modal__footer">
        <ld-button
          neutral
          :disabled="isCancelDisabled"
          @click="cancel"
        >
          Cancel
        </ld-button>
        <ld-button
          brand
          :disabled="isSaveDisabled"
          @click="save"
        >
          Save
        </ld-button>
      </footer>
    </div>
  </right-page>
</template>

<script>
import { LdButton } from 'charket-ui/dist/ld-button.js'
import { LdInput } from 'charket-ui/dist/ld-input.js'
import { PardotService } from '../service'

export default {
  name: 'FormHandler',
  components: {
    LdButton,
    LdInput
  },
  data () {
    return {
      rightPageHeader: {
        label: 'CHARKET SETTINGS',
        name: 'Form Handler'
      },
      endpointURL: '',
      isSaving: false,
      originalEndpointURL: ''
    }
  },
  computed: {
    hasChanged () {
      return this.originalEndpointURL !== this.endpointURL
    },
    isUrlLegal () {
      return !this.endpointURL ||
        /^(https:\/\/)/.test(this.endpointURL)
    },
    isCancelDisabled () {
      return !this.hasChanged ||
        this.isSaving
    },
    isSaveDisabled () {
      return !this.hasChanged ||
        this.isSaving ||
        !this.isUrlLegal
    }
  },
  created () {
    this.fetchFormHandler()
  },
  methods: {
    async fetchFormHandler () {
      this.$bus.emit('show-loading')
      await this.$try(async () => {
        this.endpointURL = await PardotService.getFormHandler()
        this.originalEndpointURL = this.endpointURL
      })
      this.$bus.emit('hide-loading')
    },
    cancel () {
      this.endpointURL = this.originalEndpointURL
    },
    async save () {
      this.isSaving = true
      await this.$try(async () => {
        await PardotService.saveFormHandler(this.endpointURL)
        this.$bus.emit('success', { content: 'Form Handler was saved.', type: 'success' })
        this.originalEndpointURL = this.endpointURL
      })
      this.isSaving = false
    }
  }
}
</script>

<style lang="scss" scoped>
  .section {
    padding: 20px 30px;
    .section-content {
      width: 448px;
    }
  }
</style>
